import React, {Component} from "react";
import PropTypes from 'prop-types'
import _ from 'lodash';

import FixSecondaryChoiceBonusModal from "../FixSecondaryChoiceBonusModal";
import {FixAnalyzerAdapter} from "../AnalyzerAdapter";
import {ButtonCell, FloatingBox, LabelCell} from "../../BoxFragments";
import {GraphicCell} from "../../Utils";
import {createCustomRules} from "../../../../../analyzer/DeviceUtils"

const preventDefaultWrapper = (handler) => (event) => {
    event.preventDefault();
    handler(event);
}

export default class AddBonusButton extends Component {

    constructor(props){
        super(props);
        this.addButtonClicked = this.addButtonClicked.bind(this);
        this.closeModalClicked = this.closeModalClicked.bind(this);
    }

    addButtonClicked(){
        console.log("Modal clicked");
        this.props.modalOpenAction();
    }

    closeModalClicked(){
        this.props.modalCloseAction();
    }

    removeButtonClicked(productCode){
        console.log("Remove button clicked: "+productCode);
        if(this.adapter && this.adapter.removeProduct){
            this.adapter.removeProduct(productCode);
        }
    }

    render(){
        const presentable = _.flatMap(this.props.presentable);
        return(
            <div>
                <FloatingBox boxClassName="u-padding-left-l u-padding-right-l u-box-shadow--s u-box-shadow-hover">
                    <a id={`add-bonus-button-${this.props.idx}`} href="#" onClick={preventDefaultWrapper(this.addButtonClicked)}>
                        <table className="u-table-fixed">
                            <tbody>
                            <tr>
                                <GraphicCell icon={this.props.icon}/>
                                <LabelCell label={this.props.label}/>
                                <ButtonCell icon={"plus"} link={"#"}/>
                            </tr>
                            </tbody>
                        </table>
                    </a>
                </FloatingBox>
                <FixAnalyzerAdapter
                    configurables={this.props.configurables}
                    entry={this.props.entry}
                    customRules={createCustomRules(presentable, this.props.migrated)}
                    ref={(adapter) => this.adapter = adapter}
                >
                    <FixSecondaryChoiceBonusModal
                        uid={`FBM_${this.props.uid}`}
                        label={this.props.header}
                        open={this.props.isModalOpen}
                        services={presentable}
                        bonus={this.props.bonus}
                        onClose={this.closeModalClicked}
                        descriptions={this.props.descriptions}
                        showNetPrices={this.props.showNetPrices}
                        isActivateSecondaryChoice={false}
                        configurables={this.props.configurables}
                        migratedProducts={this.props.migrated}
                    />
                </FixAnalyzerAdapter>
            </div>
        );
    }
}

AddBonusButton.propTypes = {
    icon: PropTypes.string,
    label: PropTypes.string,
    configurables: PropTypes.object.isRequired,
    entry: PropTypes.object.isRequired,
    presentable: PropTypes.array,
    bonus: PropTypes.object.isRequired,
    migrated: PropTypes.array,
    uid: PropTypes.string,
    header: PropTypes.string
};
